import React, { useState } from "react";
import Image from "../../shared/Image";
import { Link } from "react-router-dom";
import Date from "../../shared/Date";
import authAxios from "../../../modules/shared/axios/authAxios";
import Translate from "../../shared/Translate";
import { baseUrl } from "../../shared/BaseUrl";
import { i18n } from "../../../i18n";
function PublicationDetaillByThematique(props) {
  const { data, index, thematique } = props;
  const language = localStorage.getItem("language");

  const renderItem = (item) => {
    return (
      <div>
        <Link to={`/detail/${item.id}`}>
          {language === "fr" ? (
            <Image
              src={
                item.photos.length > 0
                  ? `${baseUrl}/file/download?privateUrl=${item.photos[0]?.privateUrl}`
                  : "Image publication image "
              }
              width="370"
              height="300"
              alt="Image publication image "
            />
          ) : language === "ar" ? (
            <Image
              src={
                item.photosAR.length > 0
                  ? `${baseUrl}/file/download?privateUrl=${item.photosAR[0]?.privateUrl}`
                  : "Image publication image "
              }
              width="370"
              height="300"
              alt="Image publication image "
            />
          ) : (
            <Image
              src={
                item.photosEN.length > 0
                  ? `${baseUrl}/file/download?privateUrl=${item.photosEN[0]?.privateUrl}`
                  : "Image publication image "
              }
              width="370"
              height="300"
              alt="Image publication image "
            />
          )}

          <div className="list__detaill">
            <div className="detaill__header">
              <div></div>
              <div className="header__right">{Date.fullDate(item.date)}</div>
            </div>
            <div className="detaill__content text__wrap">
              {Translate.Trans("title", item)}
            </div>
          </div>
        </Link>
      </div>
    );
  };

  return (
    <div className="pub__migration" key={index}>
      <div className="archieve__header">
        <h2>{Translate.Trans("title", thematique)}</h2>
        <div className="satestique__bar"></div>
      </div>

      <div className="migration__images">
        {data.map((item) => renderItem(item))}
      </div>

      <Link to={`/detaill/${thematique._id}`}>
        <div className="plus__button">{i18n("common.voirPlus")}</div>
      </Link>
    </div>
  );
}

export default PublicationDetaillByThematique;
