import React from "react";
import Date from "../../shared/Date";
import Translate from "../../shared/Translate";
import { baseUrl } from "../../shared/BaseUrl";
import { Link } from "react-router-dom";

function PublicationDetaill(props) {
  const { data } = props;
  const language = localStorage.getItem("language");
  return (
    <div className="pub__relative" key={props.key}>
      <>
        {language === "fr" ? (
          <img
            src={
              data.photos.length > 0
                ? data.photos[0]?.downloadUrl
                : "Image publication image "
            }
            alt="header"
          />
        ) : language === "ar" ? (
          <img
            src={
              data.photosAR.length > 0
                ? data.photosAR[0]?.downloadUrl
                : "Image publication image "
            }
            alt="header"
          />
        ) : (
          <img
            src={
              data.photosEN.length > 0
                ? data.photosEN[0]?.downloadUrl
                : "Image publication image "
            }
            alt="header"
          />
        )}
      </>

      <Link to={`/detail/${data.id}`}>
        <div className="__detaills">
          <div className="list__detaill">
            <div className="detaill__header">
              <div className="__left">
                <p> {Translate.Trans("title", data.thematique[0])}</p>
              </div>
              <div className="__right">{Date.fullDate(data.date)}</div>
            </div>
            <div className="__content text__wrap" style={{ maxWidth: 250 }}>
              {Translate.Trans("title", data)}
            </div>
          </div>
        </div>
      </Link>
    </div>
  );
}
export default PublicationDetaill;
